import React, { Component } from 'react';

export default class About extends Component {
  render() {
    return (
      <div>
        <h2>Jaiminho  um projeto desenvolvido para rastrear encomendas atravs dos correios de uma forma rpida e fcil.</h2><br/>
        <p>Utilizamos o webservice dos correios para obter as informaes e as mesmas no so armazenadas.</p><br/>
        <p><strong>Dica: Voc pode adicionar a url gerada na consulta em seus favoritos ;)</strong></p>
      </div>
    )
  }
}



